@Component({
  selector: 'hard-copy-statement-request',
  templateUrl: './hard-copy-statement-request.page.html',
})
export class HardCopyStatementRequestPage extends OmniBasePage implements OnInit {
  HardCopyRequestVO: any = {};
  filterDate: IOptionsPSComplexDateFilter = {};
  accountCardItem: any;
  lookupDetails: IOptionsPsContainerLookupOptionComponentExposed = {
    singleLookup: true,
    labelKey: 'account_name_key'
  };
  public labelValuesMap = new Map<string, IMapKeyValue>();
  public headerMap = new Map<string, IMapKeyValue>();
  public requestMap = new Map<string, string>();
  public editRequestMap = new Map<string, string>();
  sysParamScreenDispList = new Map<string, any>(); //object used for storing customization values from OADM
  constructor(public logger: LoggerService, private omniPull: OmniPullService, private navService: PsNavigatorService, public eventEmitterService: EventEmitterService) {
    super();
  }

  private formGroup = new FormGroup({});
  public options: IOptionsTemplateStepper = {
    stepperName: 'hard_copy_request_stepper',
    numberOfSteps: 2,
    group: this.formGroup,
    submitOptions: {
      submitServiceUrl: PsCommonSettings.serviceUrl.commonSubmitAction,
      group: this.formGroup,
      preCallFunction: {
        func(response) {
          return new Promise<any>((resolve, reject) => {
            resolve(this.executionClass.updateAccountVO(response));
          });
        },
        executionClass: this,
        params: [this]
      }
    },
    requestObject: this.HardCopyRequestVO,
    isHorizontalStepper: true,
    namesofSteps: ['hard_copy_statement_step_1', 'map_hard_copy_statement_step_2']
  };

  panelOptions1: IOptionsPsContainerPanel = {
    isExpandable: true,
    labelKey: 'account_details_key',
    iconName: 'document',
    expanded: true
  };

  panelOptions2: IOptionsPsContainerPanel = {
    isExpandable: true,
    labelKey: 'statement_details_key',
    iconName: 'document',
    expanded: true
  };

  panelOptions3: IOptionsPsContainerPanel = {
    isExpandable: true,
    labelKey: 'collection_delivery_key',
    iconName: 'box',
    expanded: true
  };

  panelOptions4: IOptionsPsContainerPanel = {
    isExpandable: true,
    labelKey: 'collector_receiver_key',
    iconName: 'document',
    expanded: true
  };

  panelOptions5: IOptionsPsContainerPanel = {
    isExpandable: true,
    labelKey: 'attachment_key',
    iconName: 'file',
    expanded: true
  };
  panelOptionsStep2: IOptionsPsContainerPanel = {
    isExpandable: true,
    labelKey: 'collection_deliv_branch_key',
    iconName: 'file',
    expanded: true
  };

  additionalCommentsOptions: IOptionsPsKeyinTextarea = {
    labelKey: 'additional_comments_key',
    placeHolder: 'enter_additional_comments_key',
    rows: '3',
    cols: '10',
    fcName: 'additionalComments',
    group: this.formGroup
  };
  deliveryAdditionalCommentsOptions: IOptionsPsKeyinTextarea = {
    labelKey: 'delivery_additional_cmnts_key',
    placeHolder: 'enter_delivery_cmnts_key',
    rows: '3',
    cols: '10',
    fcName: 'deliveryAdditionalComments',
    group: this.formGroup
  };

  collectorNameOptions: IOptionsPsInputVarcharExposed = {
    group: this.formGroup,
    fcName: 'collectorName',
    labelKey: 'collector_name_key',
    placeHolder: 'enter_collector_name_key'
  }
  complexIdDetailsOptions: IOptionsPsComplexIdDetailsExposed = {

    dropdownIdTypesOptions: {
      fcName: 'idType',
      group: this.formGroup,
    },
    idNumberOptions: {
      labelKey: 'id_number_key',
      placeHolder: 'enter_id_number_key',
      fcName: 'idNumber',
      group: this.formGroup
    },
    dateExpiryOptions: {
      labelKey: 'expiry_date_key',
      placeHolder: 'enter_expiry_date_key',
      fcName: 'idExpiryDate',
      group: this.formGroup,
    },
    countriesOptions: {
      placeHolder: 'country_of_issuance_key',
      labelKey: 'country_of_issuance_key',
      fcName: 'idCountryOfIssuance',
      group: this.formGroup,
    },
    issueDateOptions: {
      labelKey: 'issue_date_key',
      placeHolder: 'issue_date_key',
      fcName: 'idIssueDate',
      group: this.formGroup
    }
  };
  collectedByCifOptions: IOptionsPsSelectToggle = {
    labelKey: 'collecting_toggle_message_key',
    group: this.formGroup,
    fcName: 'isAmountCollectedByCif',
    psClass: 'toggleColor'
  };
  fileUploadOptions: IOptionsPsFileUploadComponent = {
    multiple: false,
    fcName: 'uploadedFiles',
    uploadIconName: 'attach',
    group: this.formGroup

  };
  timeDeliveryOptions: IOptionsPSComplexTimeRangeExposed = {
    fromTime: {
      fcName: 'deliveryTimeFrom',
      group: this.formGroup,
      labelKey: 'from_time_key',
      placeHolder: 'select_from_time_key'
    },
    toTime: {
      fcName: 'deliveryTimeTo',
      group: this.formGroup,
      labelKey: 'to_time_key',
      placeHolder: 'select_to_time_key'
    },
    group: this.formGroup
  }
  deliveryAddressDetails: IOptionsPsComplexLocationExposed = {
    collectionModeOptions: {
      group: this.formGroup,
      fcName: 'collectionMode'
    },
    branchLocationOptions: {
      fcName: 'mapAtmBranchesDropdown',
      group: this.formGroup,
    },
    homeAddressOptions: {
      placeHolder: 'home_address_key',
      labelKey: 'home_address_key',
      group: this.formGroup,
      fcName: 'deliveryHomeAddress',
      requestVO: this.HardCopyRequestVO
    },
    workAddressOptions: {
      placeHolder: 'work_address_key',
      labelKey: 'work_address_key',
      group: this.formGroup,
      fcName: 'deliveryWorkAddress',
      requestVO: this.HardCopyRequestVO
    },
    otherAddressOptions: {
      placeHolder: 'enter_address_key',
      labelKey: 'other_address_key',
      group: this.formGroup,
      fcName: 'deliveryOtherAddress',
    },
    cifBranchChecking: false
  };

  deliveryDateOptions: IOptionsPsDateMonthYearFutureExposed = {
    labelKey: 'delivery_date_key',
    placeHolder: 'select_delivery_date_key',
    group: this.formGroup,
    fcName: 'deliveryDate'
  }
  mapAtmBranchesOptions: IOptionsPsMapAtmBranchesExposed = {
    group: this.formGroup,
    fcName: 'mapAtmBranches',
    labelKey: 'map_atm_branch_key',
    mapOptions: {
      labelKey: 'map'
    },
    showSegments: false,
    mapTypesInclude: '\'B\'',
    branchIds: '1,3,5',
    parameterToCheck: CommonBussinessConstant.RequestBranch,
    requestVO: this.HardCopyRequestVO
  };

  ngOnInit() {
    super.init();
    this.baseFormGroup = this.formGroup;
    this.sysParamScreenDispList = this.commonProv.initialScreenDisplayParams.get(PsCommonSettings.oper_ID);
    this.commonProv.copyObject(this.HardCopyRequestVO, this.navService.getAllParams() ? this.navService.getAllParams().ScreenVO ? this.navService.getAllParams().ScreenVO : this.navService.getAllParams() : {}, true, false);
    this.hideMapStep();
    this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_VISIBLE, [this.options.namesofSteps[1]], 0);

    if (!this.HardCopyRequestVO || this.HardCopyRequestVO['isAmountCollectedByCif'] == undefined) {
      this.HardCopyRequestVO['isAmountCollectedByCif'] = true;
    }

    const param = this.omniPull.omniCommon.session.getValueOf(ConstantCommon.preLoginResponse).parameters;
    const defaultFormat = param.DefaultDateFormat ? param.DefaultDateFormat.parameterVal : 'DD/MM/YYYY';

    this.filterDate = {
      fromDate: {
        group: this.formGroup,
        fcName: 'fromDate',
        displayFormat: defaultFormat,
        placeHolder: 'from_date_key',
      },
      toDate: {
        group: this.formGroup,
        fcName: 'toDate',
        displayFormat: defaultFormat,
        placeHolder: 'to_date_key',
      }
    };

    this.populateAccountsMap();

  }
  ionViewDidEnter() {
    super.ionViewDidEnter();

    // toggle button by default enabled
    this.commonProv.setValInsideNestedObj(this.collectedByCifOptions.fcName, true, this.HardCopyRequestVO);
    this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_MANDATORY, [this.collectorNameOptions.fcName], 0, true);
    this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_MANDATORY, [this.collectedByCifOptions.fcName], 0, true);
    this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_MANDATORY, [this.complexIdDetailsOptions.idNumberOptions.fcName], 0, true);
    this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_MANDATORY, [this.complexIdDetailsOptions.dropdownIdTypesOptions.fcName], 0, true);

  }
  private balanceMapping(): any[] {
    const balanceMappingList = [
      {
        key: 'current_balance_key',
        value: 'balance'
      },
      {
        key: 'available_balance_key',
        value: 'availableBalance'
      },
      {
        key: 'currency_iso_key',
        value: 'currencyBriefNameEnglish'
      }
    ];

    return balanceMappingList;
  }


  private populateAccountsMap() {
    let accountNumberMap: IMapKeyValue = {};
    let accountBalanceMap: IMapKeyValue = {};
    let investMentCapitalMap: IMapKeyValue = {};
    let profitRateMap: IMapKeyValue = {};
    let tenorMap: IMapKeyValue = {};
    let maturityDateMap: IMapKeyValue = {};
    let dividendsPaidMap: IMapKeyValue = {};
    let accruedDividendsMap: IMapKeyValue = {};
    let renewMap: IMapKeyValue = {};
    let renewWithSameMap: IMapKeyValue = {};
    let headerTitleMap: IMapKeyValue = {};
    let headerSubtitleMap: IMapKeyValue = {};

    accountNumberMap = {
      key: 'formattedAccount', // will be used in lookup compoent to get the value of account nummer from list of accounts
      value: '--- --- --- ---',
      isEdit: false,
    };

    accountBalanceMap = {
      key: 'balance', // will be used in lookup compoent to get the value of account balance from list of accounts
      value: '0.0',
      isEdit: false,
    };


    investMentCapitalMap = {
      key: 'investmentCapital',
      value: '0',
      isEdit: false
    };

    profitRateMap = {
      key: 'profitRate',
      value: '0',
      isEdit: false
    };

    tenorMap = {
      key: 'tenor',
      value: '0',
      isEdit: false
    };

    maturityDateMap = {
      key: 'maturityDate',
      value: '0',
      isEdit: false
    };

    dividendsPaidMap = {
      key: 'dividendsPaid',
      value: '0',
      isEdit: false
    };

    accruedDividendsMap = {
      key: 'accruedDividends',
      value: '0',
      isEdit: false
    };

    renewMap = {
      key: 'renew',
      value: '0',
      isEdit: false
    };

    renewWithSameMap = {
      key: 'renewWithSame',
      value: '0',
      isEdit: false
    };


    headerTitleMap = {
      key: 'title',
      value: 'generalLedgerBriefNameEng',
      isEdit: false,
    };

    headerSubtitleMap = {
      key: 'subTitle',
      value: 'briefName',
      isEdit: true,
      formGroupMap: {
        placeHolder: 'enter_account_nick',
        fcName: 'briefName',
      }
    };

    this.labelValuesMap.set('account_key', accountNumberMap);
    if (this.HardCopyRequestVO.isFMA && (this.commonProv.getPageByOperId(ConstantCommon.MATURITY_UPDATE_OPER_ID) !== undefined)) {
      this.labelValuesMap.set('investment_capital_key', investMentCapitalMap);
      this.labelValuesMap.set('profit_rate_key', profitRateMap);
      this.labelValuesMap.set('tenor_key', tenorMap);
      this.labelValuesMap.set('maturity_date_key', maturityDateMap);
      this.labelValuesMap.set('dividends_paid_key', dividendsPaidMap);
      this.labelValuesMap.set('accrued_dividends_key', accruedDividendsMap);
      this.labelValuesMap.set('renew_key', renewMap);
      this.labelValuesMap.set('renew_new_balance_within_same_account_key', renewWithSameMap);
    }

    this.headerMap.set('title', headerTitleMap);
    this.headerMap.set('subTitle', headerSubtitleMap);
    this.requestMap.set('nickNameNumber', 'key');
    this.requestMap.set('oldNickName', 'briefName');
    this.editRequestMap.set('newNickName', 'briefName');
    this.editRequestMap.set('type', 'A');

    this.lookupDetails.fcName = 'fromAccount';
    this.lookupDetails.currencyFlag = 'currencyBriefNameEnglish';
    this.lookupDetails.labelsValueMap = this.labelValuesMap;
    this.lookupDetails.headerMap = this.headerMap;
    this.lookupDetails.formGroup = this.options.group;
    this.lookupDetails.balanceMapping = this.balanceMapping();
    this.lookupDetails.isEditable = false;
    this.lookupDetails.requestMap = this.requestMap;
    this.lookupDetails.editRequestMap = this.editRequestMap;

    this.lookupDetails.actionDetailsOptions = [];
    this.lookupDetails.statementOptions = {};

    this.accountCardItem = this.HardCopyRequestVO;
  }
  hideMapStep() {
    let branchLocatorStepperName = this.options.namesofSteps[1];
    let modeOfCollection = this.HardCopyRequestVO ? this.HardCopyRequestVO[this.deliveryAddressDetails.collectionModeOptions.fcName] : {};
    if ((modeOfCollection && modeOfCollection?.itemValue === CommonBussinessConstant.SPECIFIC_BRANCH_VALUE || modeOfCollection === CommonBussinessConstant.SPECIFIC_BRANCH_VALUE) || modeOfCollection === undefined) {
      //  id: 1260745 -  Details :Updated by neethu  - Date:01/10/2021 - Desc:Customization should be applied based on the  customization values from OADM,so before applying customization to map_hard_copy_statement_step_2 need to check the customization value from OADM also 
      if (!(this.sysParamScreenDispList && this.sysParamScreenDispList.get(branchLocatorStepperName) && this.sysParamScreenDispList.get(branchLocatorStepperName).IS_VISIBLE == 0)) {
        this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_VISIBLE, [branchLocatorStepperName], 1, true);
        this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_MANDATORY, ['mapAtmBranches'], 1, true);
      }
    } else if (modeOfCollection !== undefined) {
      this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_VISIBLE, [branchLocatorStepperName], 0, true);
    }
  }

  toggleChanged(event) {
    if (event.newValue == true) {
      this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_MANDATORY, [this.collectorNameOptions.fcName], 0, true);

      this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_MANDATORY, [this.collectorNameOptions.fcName], 0, true);
      this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_MANDATORY, [this.complexIdDetailsOptions.idNumberOptions.fcName], 0, true);
      this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_MANDATORY, [this.complexIdDetailsOptions.dropdownIdTypesOptions.fcName], 0, true);

    } else {
      this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_MANDATORY, [this.collectorNameOptions.fcName], 1, true);

      this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_MANDATORY, [this.collectorNameOptions.fcName], 1, true);
      this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_MANDATORY, [this.complexIdDetailsOptions.idNumberOptions.fcName], 1, true);
      this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_MANDATORY, [this.complexIdDetailsOptions.dropdownIdTypesOptions.fcName], 1, true);
    }
  }

  updateAccountVO(data) {
    this.HardCopyRequestVO['fromAccount'] = { ...this.HardCopyRequestVO }
  }
}
